import React, { useState } from 'react';
import PropTypes from 'prop-types';
import WcIcon from '@material-ui/icons/Wc';
import Restaurant from '@material-ui/icons/Restaurant';
// import HotelIcon from '@material-ui/icons/Hotel';
import FreeBreakfastIcon from '@material-ui/icons/FreeBreakfast';
// import ApartmentIcon from '@material-ui/icons/Apartment';
import NotListedLocationIcon from '@material-ui/icons/NotListedLocation';
import AccessibleIcon from '@material-ui/icons/Accessible';
import ChildFriendlyIcon from '@material-ui/icons/ChildFriendly';
import AttachMoneyOutlinedIcon from '@material-ui/icons/AttachMoneyOutlined';
import MoneyOffOutlinedIcon from '@material-ui/icons/MoneyOffOutlined';
import ArrowForwardIosOutlinedIcon from '@material-ui/icons/ArrowForwardIosOutlined';

const NearestLocations = ({ locations, reCenter }) => {
  const [showNearest, setShowNearest] = useState(false);

  const styles = {
    container: {
      position: 'fixed',
      bottom: 0,
      height: showNearest ? '45%' : '8%',
      width: '98%',
      backgroundColor: '#fff',
      zIndex: 1,
      opacity: '.9',
    },
    header: {
      height: showNearest ? '20%' : '100%',
      color: '#fff',
      backgroundColor: '#85cad4',
      borderTopLeftRadius: '3px',
      borderTopRightRadius: '3px',
      icons: {
        display: 'flex',
        justifyContent: 'center',
      },
      distance: {
        display: 'flex',
        justifyContent: 'flex-end',
      },
    },
    body: {
      height: '80%',
      display: 'grid',
    },
    grid: {
      display: 'grid',
      gridTemplateColumns: '40% 40% 15% 5%',
      gridTemplateRows: 'auto',
      alignItems: 'center',
      fontSize: '.9rem',
      padding: '.5rem',
      icons: {
        display: 'flex',
        justifyContent: 'center',
        alignItem: 'center',
      },
      distance: {
        display: 'flex',
        justifyContent: 'flex-end',
        alignItem: 'center',
      },
      item: {
        borderBottom: '1px solid #C1E9E4',
      },
    },
  };

  const icons = {
    public: <WcIcon fontSize="small" />,
    restaurant: <Restaurant fontSize="small" />,
    cafe: <FreeBreakfastIcon fontSize="small" />,
    other: <NotListedLocationIcon fontSize="small" />,
    payable: <AttachMoneyOutlinedIcon fontSize="small" />,
    free: <MoneyOffOutlinedIcon fontSize="small" />,
  };

  const toggleNearest = () => {
    setShowNearest(!showNearest);
  };

  return (
    <div
      style={styles.container}
      role="button"
      tabIndex={0}
      className="nearest-locations-container"
    >
      <div
        style={{ ...styles.header, ...styles.grid }}
        role="button"
        tabIndex={0}
        className="nearest-locations-header"
        onClick={toggleNearest}
      >
        <div className="nearest-locations-header-name">
          {locations.length ? locations[0].name : null}
        </div>
        {locations[0] && (
          <div className="nearest-locations-header-icons" style={styles.grid.icons}>
            <div>{icons[locations[0].type]}</div>
            <div>{icons[locations[0].payable]}</div>
            <div>
              {locations[0].services.length &&
                locations[0].services.map((service) =>
                  service === 'inva' ? (
                    <AccessibleIcon key={service} fontSize="small" />
                  ) : (
                    <ChildFriendlyIcon key={service} fontSize="small" />
                  )
                )}
            </div>
          </div>
        )}
        <div>
          {locations.length ? (
            <div className="nearest-locations-header-distance" style={styles.grid.distance}>
              {locations[0].distance <= 1000
                ? `${locations[0].distance.toFixed(0)}m`
                : `${(locations[0].distance / 1000).toFixed(1)}km`}
            </div>
          ) : null}
        </div>
      </div>

      <div className="nearest-locations-body" style={styles.body}>
        {showNearest &&
          locations.slice(1, 4).map((location) => (
            <div
              key={location.id || location.lat}
              role="button"
              tabIndex={0}
              className="nearest-locations-body-item"
              style={{ ...styles.grid, ...styles.grid.item }}
              onClick={() => reCenter(location)}
            >
              <div>{location.name}</div>
              <div style={styles.grid.icons}>
                <div>{icons[location.type]}</div>
                <div>{icons[location.payable]}</div>
                <div>
                  {locations && location.services.length
                    ? location.services.map((service) =>
                        service === 'inva' ? (
                          <AccessibleIcon key={service} fontSize="small" />
                        ) : (
                          <ChildFriendlyIcon key={service} fontSize="small" />
                        )
                      )
                    : null}
                </div>
              </div>
              <div style={styles.grid.distance}>
                {location.distance <= 1000
                  ? `${location.distance.toFixed(0)}m`
                  : `${(location.distance / 1000).toFixed(1)} km`}
              </div>
            </div>
          ))}
      </div>
    </div>
  );
};

NearestLocations.propTypes = {
  locations: PropTypes.instanceOf(Array).isRequired,
  reCenter: PropTypes.func.isRequired,
};

export default NearestLocations;
