import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Rating from '@material-ui/lab/Rating';
import { FaPoop } from 'react-icons/fa';
import WcIcon from '@material-ui/icons/Wc';
import Restaurant from '@material-ui/icons/Restaurant';
// import HotelIcon from '@material-ui/icons/Hotel';
import FreeBreakfastIcon from '@material-ui/icons/FreeBreakfast';
// import ApartmentIcon from '@material-ui/icons/Apartment';
import NotListedLocationIcon from '@material-ui/icons/NotListedLocation';
import AccessibleIcon from '@material-ui/icons/Accessible';
import ChildFriendlyIcon from '@material-ui/icons/ChildFriendly';
import AttachMoneyOutlinedIcon from '@material-ui/icons/AttachMoneyOutlined';
import MoneyOffOutlinedIcon from '@material-ui/icons/MoneyOffOutlined';
import locationService from '../services/locations';
// import RestaurantIcon from '@material-ui/icons/Restaurant';
// import HotelIcon from '@material-ui/icons/Hotel';
// import FreeBreakfastIcon from '@material-ui/icons/FreeBreakfast';
// import ApartmentIcon from '@material-ui/icons/Apartment';

const PoopRating = withStyles({
  iconFilled: {
    color: '#8c4a38',
  },
})(Rating);

const SelectedLocation = ({ locationDetails, selectLocation, updateLocation }) => {
  const [rating, setRating] = useState(locationDetails.rating || 3);
  // const [rating, setRating] = useState(null);
  const [rated, setRated] = useState(false);

  const styles = {
    container: {
      backgroundColor: '#fff',
      height: '45vh',
      width: '98%',
      position: 'absolute',
      bottom: 0,
      left: '50%',
      transform: 'translate(-50%, 0)',
      zIndex: 6,
    },
    header: {
      display: 'flex',
      justifyContent: 'space-between',
      padding: '.8rem 1rem .8rem 1rem',
      height: '20%',
      color: '#fff',
      marginBottom: '1rem',
      backgroundColor: '#85cad4',
      borderTopLeftRadius: '3px',
      borderTopRightRadius: '3px',
      opacity: '.87',
      itemsRight: {
        display: 'flex',
        item: {
          marginLeft: '1rem',
        },
      },
    },
    body: {
      display: 'grid',
      gridTemplateColumns: '1fr',
      gridTemplateRows: '3fr',
      paddingRight: '1rem',
      paddingLeft: '1rem',
      item: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        padding: '.5rem 0 .5rem 0',
        borderBottom: '1px solid #C1E9E4',
        margin: {
          marginRight: '.5rem',
        },
        services: {
          display: 'flex',
        },
        rating: {
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          padding: '1rem 0 .5rem 0',
        },
      },
    },
  };

  const selectIcon = () => {
    switch (locationDetails.type) {
      case 'public':
        return <WcIcon />;
      case 'inva':
        return <AccessibleIcon />;
      default:
        return <NotListedLocationIcon />;
    }
  };

  const icons = {
    public: <WcIcon fontSize="small" />,
    restaurant: <Restaurant fontSize="small" />,
    cafe: <FreeBreakfastIcon fontSize="small" />,
    other: <NotListedLocationIcon fontSize="small" />,
    payable: <AttachMoneyOutlinedIcon fontSize="small" />,
    free: <MoneyOffOutlinedIcon fontSize="small" />,
  };

  const translations = {
    public: 'Julkinen',
    restaurant: 'Ravintola',
    cafe: 'Kahvila',
    other: 'Ei tiedossa',
    payable: 'Kyllä',
    free: 'Ei',
  };

  const handleRating = async (e) => {
    const ratedLocation = {
      ...locationDetails,
      ratings: locationDetails.ratings + 1,
      rating: +e.target.value + locationDetails.rating,
    };

    const response = await locationService.updateLocation(ratedLocation);
    setRating(response.rating);
    updateLocation(response);
    selectLocation(response);
    setRated(true);
  };

  console.log();

  return (
    <div className="selected-location-container" style={styles.container}>
      <div
        className="selected-location-header"
        style={styles.header}
        role="button"
        tabIndex={0}
        onClick={() => selectLocation()}
      >
        <div className="selected-location-header-name">{locationDetails.name}</div>
        <div style={styles.header.itemsRight}>
          <div className="selected-location-header-distance" style={styles.header.itemsRight.item}>
            {locationDetails.distance.toFixed(2)}
          </div>
        </div>
      </div>
      <div className="selected-location-body" style={styles.body}>
        <div className="selected-location-type" style={styles.body.item}>
          <div>Tyyppi:</div>
          <div style={styles.body.item.services}>
            <div style={styles.body.item.margin}>{translations[locationDetails.type]}</div>
            <div>{icons[locationDetails.type]}</div>
          </div>
        </div>
        <div className="selected-location-payable" style={styles.body.item}>
          <div>Maksullinen:</div>
          <div style={styles.body.item.services}>
            <div style={styles.body.item.margin}>{translations[locationDetails.payable]}</div>
            <div>{icons[locationDetails.payable]}</div>
          </div>
        </div>
        {locationDetails.services && (
          <div
            key={locationDetails.id}
            className="selected-location-services-container"
            style={styles.body.item}
          >
            <div className="selected-location-services-title">Lisäpalvelut:</div>
            <div className="selected-locations-services-icons" style={{ display: 'flex' }}>
              {locationDetails.services.map((service) => (
                <div key={service}>
                  {service === 'inva' ? <AccessibleIcon /> : <ChildFriendlyIcon />}
                </div>
              ))}
            </div>
          </div>
        )}
        <div style={styles.body.item.rating}>
          <PoopRating
            name="rating"
            value={rating}
            onChange={(e) => handleRating(e)}
            icon={<FaPoop size="1.8rem" style={{ marginRight: '.1rem' }} />}
            disabled={rated}
          />
          <div>{locationDetails.rating ? locationDetails.rating.toFixed(1) : 'Ei Arvioitu'}</div>
        </div>
      </div>
    </div>
  );
};

SelectedLocation.defaultProps = {
  locationDetails: null,
};

SelectedLocation.propTypes = {
  locationDetails: PropTypes.instanceOf(Object),
  selectLocation: PropTypes.func.isRequired,
  updateLocation: PropTypes.func.isRequired,
};

export default SelectedLocation;
